import React from 'react';

import { stateType } from '../persistence/state-types';
import { getHomeResources } from '../utils/resource-loader';
import { withStyles } from '../utils/styles';

export const ID: stateType.View = {id: 'HOME', title: 'Home'};

const styles = (theme: any): object => ({
  root: {
    position: 'relative'
  },
  container: {
    position: 'relative'
  },
  teacher: {
    display: 'block',
    width: '100%'
  },
  whiteboard: {
    position: 'absolute',
    left: '7%',
    top: '17%',
    overflow: 'overlay',
    fontSize: theme.typography.pxToRem(16),
    wordWrap: 'break-word',
    width: '46%',
    height: '38%',
    color: 'black'
  }
});


type Classes = {
  root: string,
  container: string,
  teacher: string,
  whiteboard: string
};

type Props = {
  classes: Classes,
  mode: stateType.Mode
};

class Home extends React.Component<Props> {
  render() {
    const { classes, mode } = this.props;
    const { text, image } = getHomeResources(mode);
    return (
    <div className={classes.root}>
      <div className={classes.container}>
        <img className={classes.teacher} src={image} alt='teacher' />
        <div className={classes.whiteboard}>
        {text}
        </div>
      </div>
    </div>
    );
  }
}
export default withStyles(styles)(Home);