import { ExpansionPanel, ExpansionPanelSummary, Typography } from '@material-ui/core';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';
import React from 'react';

import { assistanceType } from '../../persistence/state-types';
import { withStyles } from '../../utils/styles';

const assistStyles = theme => ({
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
});

type AssistClasses = {
  heading: string,
  secondaryHeading: string
};

type AssistProps = assistanceType.Assist & { classes: AssistClasses };

class _Assist extends React.Component<AssistProps> {
  render() {
    const { classes, name, points, description } = this.props;
    return (
      <ExpansionPanel expanded={false}>
        <ExpansionPanelSummary expandIcon={<LocalAtmIcon />}>
          <Typography className={classes.heading}>{name} (Pay {points} Nectar Points)</Typography>
          <Typography className={classes.secondaryHeading}>{description}</Typography>
        </ExpansionPanelSummary>
      </ExpansionPanel>
    );
  }
}

const Assist = withStyles(assistStyles)(_Assist);

const styles = theme => ({
  description: {
    margin: theme.spacing(1)
  }
});

type Classes = {
  description: string
};

type Props = {
  className?: string,
  classes: Classes,
  assistance: assistanceType.Assistance
};

class Assistance extends React.Component<Props> {
  render() {
    const { classes, assistance: { description, assistance = [] } } = this.props;
    return (
      <div {...this.props}>
        <Typography className={classes.description}>{description}</Typography>
        {assistance.map(({name, points, description}, index) => <Assist key={index} name={name} points={points} description={description} />)}
      </div>
    );
  }
}

export default withStyles(styles)(Assistance);