import queryString from 'query-string';
import { BehaviorSubject } from 'rxjs';
import { skip } from 'rxjs/operators';

import { TEXTS } from '../resources/exercises';
import { changeFactory } from './state';

const STORE_KEY = 'classics';

const defaultState = {};

const windowParams = queryString.parse(window.location.search);

if (windowParams.clearEverything === 'true') {
  if (window.confirm('Are you sure you want to clear everything?')) {
    localStorage.removeItem(STORE_KEY);
    alert('Cleared!');
  }
  window.location.href = window.location.href.split('?')[0];
}

function loadPersistedState() {
  try {
    const cached = localStorage.getItem(STORE_KEY);
    return !!cached ? JSON.parse(cached) : defaultState;
  } catch (e) {
    console.log('Error getting internal state, returning default');
    return defaultState;
  }
}

function loadState() {
  const state = loadPersistedState();
  Object.keys(TEXTS).forEach(level => {
    if (!state[level]) {
      state[level] = {};
      const texts = TEXTS[level];
      texts.forEach(({ID}) => {
        if (!state[level][ID]) {
          state[level][ID] = {};
        }
      })
    }
  });
  return state;
}

const storeSubject = new BehaviorSubject(loadState());
export const store$ = storeSubject.asObservable();

store$.pipe(
  skip(1)
).subscribe(state => {
  // console.log(state);
  localStorage.setItem(STORE_KEY, JSON.stringify(state));
});

changeFactory(store$).subscribe(state => storeSubject.next(state));