import React from 'react';

import { stateType } from '../persistence/state-types';
import { classNames, DefaultClasses, withStyles } from '../utils/styles';
import { getView } from '../views';

const styles = theme => ({
  root: {
    margin: 16
  }
})

type Classes = {
  root: string
};

type Props = {
  classes: Classes & DefaultClasses,
  navigation: stateType.Navigation,
  mode: stateType.Mode
};

class Content extends React.Component<Props> {
  render() {
    const { classes, navigation, mode } = this.props;
    const View = getView(navigation.currentView);
    return (
      <div className={classNames(classes.hbox, classes.root, classes.scroll)}>
        <View mode={mode} navigation={navigation} /> {/*Only for Shop Component*/}
      </div>
    );
  }
}

export default withStyles(styles)(Content);