import React from 'react';

import { missionType } from '../../persistence/state-types';
import { withStyles } from '../../utils/styles';
import Missions from './Missions';

type Props = {
  missions: missionType.Mission[]
};

class Vocabulary extends React.Component<Props> {
  render() {
    const { missions } = this.props;
    return <Missions missions={missions} />;
  }
}

export default withStyles()(Vocabulary);