import { AppBar, IconButton, Toolbar, Typography } from '@material-ui/core';
import MoonIcon from '@material-ui/icons/Brightness2';
import SunIcon from '@material-ui/icons/Brightness5';
import HomeIcon from '@material-ui/icons/Home';
import SettingsIcon from '@material-ui/icons/Settings';
import React from 'react';

import { navigateHome, showChangeModePrompt, toggleTheme } from '../persistence/state';
import { stateType } from '../persistence/state-types';
import { getTitleForLevel, getTitleForText } from '../resources/exercises';
import { DefaultClasses, withStyles } from '../utils/styles';

const styles = theme => ({
  homeButton: {
    marginRight: 20,
  },
  appBar: {
    backgroundColor: theme.palette.type === 'dark' ? theme.palette.background.paper : theme.palette.primary.main,
    color: 'white'
  }
});

type Classes = {
  homeButton: string,
  appBar: string
};

type Props = {
  classes: Classes & DefaultClasses,
  mode: stateType.Mode,
  navigation: stateType.Navigation
};

class AppToolbar extends React.Component<Props> {
  navigateHome = ():void => {
    navigateHome();
  }
  showChangeModePrompt = ():void => {
    showChangeModePrompt(true);
  }
  render() {
    const { classes, mode, navigation } = this.props;
    const title = navigation?.currentView?.title;
    const { selectedLevel, selectedText, theme } = mode;
    const usingDarkTheme: boolean = theme === 'dark';
    return (
      <AppBar position='relative' className={classes.appBar}>
        <Toolbar>
          <IconButton
            color='inherit'
            aria-label='Home'
            onClick={this.navigateHome}
            className={classes.homeButton}
          >
            <HomeIcon />
          </IconButton>
          <Typography variant='h6' color='inherit' noWrap className={classes.grow}>
            {title}
          </Typography>
          {!!selectedText && !!selectedLevel ?
            <Typography variant='subtitle1' color='inherit' noWrap >
              Level: {getTitleForLevel(selectedLevel)} Text: {getTitleForText({text: selectedText, level: selectedLevel})}
            </Typography>
            : undefined
          }
          <IconButton
            color='inherit'
            title='Settings'
            aria-label='Mode Selector'
            onClick={this.showChangeModePrompt}
          >
            <SettingsIcon />
          </IconButton>
          <IconButton
            color='inherit'
            title={usingDarkTheme ? 'Switch to light theme' : 'Switch to dark theme'}
            aria-label='Toggle Theme'
            onClick={toggleTheme}
          >
            {usingDarkTheme ? <SunIcon /> : <MoonIcon />}
          </IconButton>
        </Toolbar>
      </AppBar>
    );
  }
}

export default withStyles(styles)(AppToolbar);