import React from 'react';

import { stateType } from '../persistence/state-types';
import { getExerciseResource } from '../utils/resource-loader';

export const EXERCISE_ID: string = 'EXERCISE';
export const getExerciseId = (title: string): stateType.View => ({id: EXERCISE_ID, title });

type Props = {
  mode: stateType.Mode,
  navigation: stateType.Navigation
};

export default class Exercise extends React.Component<Props> {
  render() {
    const { mode, navigation } = this.props;
    return getExerciseResource({mode, navigation}) || null;
  }
}