import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import Typography from '@material-ui/core/Typography';
import React from 'react';

import { stateType } from '../persistence/state-types';
import { getShopContents } from '../utils/resource-loader';
import { withStyles } from '../utils/styles';

export const ID: stateType.View = {id: 'SHOP', title: 'Shop'};
const styles = theme => ({
  root: {
    marginTop: theme.spacing(3)
  },
  tile: {
    maxWidth: 200,
    minWidth: 200,
    maxHeight: 200,
    minHeight: 200
  }
});

type Classes = {
  root: string,
  tile: string
};

type Props = {
  classes: Classes,
  mode: stateType.Mode
}

class Shop extends React.Component<Props> {
  render() {
    const { classes, mode } = this.props;
    const { contents, description } = getShopContents(mode);

    return (
    <div className={classes.root}>
      <Typography variant="h5" component="h2">{description}</Typography>
      <GridList cellHeight='auto'>
        {contents.map(({image, name, price}) => (
          <GridListTile key={name} className={classes.tile}>
            <img src={image} alt={name} />
            <GridListTileBar
              title={name}
              subtitle={<span>Price: {price} NP</span>}
            />
          </GridListTile>
        ))}
      </GridList>
    </div>
    );
  }
}

export default withStyles(styles)(Shop);