import { Button, Paper, TextField, Typography } from '@material-ui/core';
import React from 'react';

import { enterPassword } from '../persistence/state';
import { stateType } from '../persistence/state-types';
import { getPasswordImageForMode } from '../utils/resource-loader';
import { classNames, DefaultClasses, withStyles } from '../utils/styles';

export const ID: stateType.View = {id: 'PASSWORD', title: 'Enter Prompt'};

const styles = theme => ({
  root: {
    margin: 40
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  paperContainer: {
    minWidth: 300,
    maxWidth: 300,
    height: 200,
    paddingTop: 20
  },
  image: {
    height: 200
  }
});

type Classes = {
  root: string,
  textField: string,
  paperContainer: string,
  image: string
};

type Props = {
  classes: Classes & DefaultClasses,
  mode: stateType.Mode
};

type State = {
  password: string
}

class Password extends React.Component<Props, State> {
  constructor(props) {
    super(props);
    this.state = {
      password: ''
    };
  }
  handleChange = event => {
    this.setState({password: event.target.value});
  }
  submitPassword = () => {
    enterPassword(this.state.password);
    this.setState({password: ''});
  }
  render() {
    const { classes, mode } = this.props;

    return (
    <div className={classNames(classes.root, classes.hbox)}>
      <Paper className={classNames(classes.paperContainer, classes.vbox)}>
        <div className={classNames(classes.vbox, classes.centered)}>
          <Typography variant='h6'>Please sign here</Typography>

          <TextField
          id='password'
          label='Prompt'
          type='password'
          className={classes.textField}
          value={this.state.password}
          onChange={this.handleChange}
          onKeyPress={ev => {
            if (ev.key === 'Enter') {
              this.submitPassword();
            }
          }}
          margin='normal'
          variant='outlined'
          />
          
        </div>
        <div className={classNames(classes.fixed, classes.hbox, classes.buttonArea)}>
          <Button color='primary' onClick={this.submitPassword} disabled={!this.state.password} value='submit'>
            Submit
          </Button>
        </div>
      </Paper>
      <img src={getPasswordImageForMode(mode)} alt='delivery' className={classes.image} />
    </div>);
  }
}

export default withStyles(styles)(Password);