import { Typography } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import React from 'react';

import { unlockShop } from '../../persistence/state';
import { shopType } from '../../persistence/state-types';
import { DefaultClasses, withStyles } from '../../utils/styles';

const styles = theme => ({
  button: {
    margin: theme.spacing(1)
  }
});

type Classes = {
  button: string
};

type Props = {
  classes: Classes & DefaultClasses,
  description: string,
  options: shopType.Option[]
}

class Shop extends React.Component<Props> {
  unlockShop = (unlock: boolean): void => {
    if (unlock) {
      unlockShop();
      alert('You have unlocked the shop!');
    } else {
      alert('No problem!');
    }
  }
  render() {
    const { classes, description, options } = this.props;
    return (
    <div className={classes.vbox}>
      <Typography>{description}</Typography>
      {options.map(({description, unlock}) => <Button key={description} variant='contained' color='primary' className={classes.button} onClick={() => this.unlockShop(unlock)}>{description}</Button>)}
    </div>
    );
  }
}

export default withStyles(styles)(Shop);