import { Divider, List, ListItem, ListItemIcon, ListItemText, ListSubheader } from '@material-ui/core';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import KeyIcon from '@material-ui/icons/VpnKey';
import React from 'react';

import { navigateExercise, navigatePassword, navigateShop } from '../persistence/state';
import { stateType } from '../persistence/state-types';
import { getExerciseIndexForPassword } from '../utils/password';
import { getExerciseResources } from '../utils/resource-loader';
import { classNames, DefaultClasses, withStyles } from '../utils/styles';

export const drawerWidth = 240;

const styles = theme => ({
  root: {
    minWidth: drawerWidth,
    maxWidth: drawerWidth,
    borderRightColor: theme.palette.divider,
    borderRightStyle: 'solid',
    borderRightWidth: '1px'
  }
});

type Classes = {
  root: string
};

type Props = {
  unlockables: stateType.Unlockables,
  mode: stateType.Mode,
  classes: Classes & DefaultClasses
};

class AppDrawer extends React.Component<Props> {

  render() {
    const { unlockables, mode, classes } = this.props;
    const { shopUnlocked = false, password } = unlockables;

    const exercises = getExerciseResources(mode);
    const unlockedIndex = getExerciseIndexForPassword({password, exercises});

    return (
      <div className={classNames(classes.root, classes.vbox)}>
        <List className={classNames(classes.vbox, classes.grow, classes.scroll)} subheader={<ListSubheader component='div'>Exercises</ListSubheader>}>
          {exercises.map(({title}, index) => index <= unlockedIndex ? (
            <List key={title}>
              <ListItem button onClick={() => navigateExercise(title)}>
                <ListItemIcon><KeyboardArrowRightIcon /></ListItemIcon>
                <ListItemText primary={title} />
              </ListItem>          
            </List>
          ) : undefined)}
        </List>
        <Divider />
        <div className={classNames(classes.vbox, classes.fixed)}>
          {
            !shopUnlocked ? undefined : (
            <List>
              <ListItem button onClick={navigateShop}>
                <ListItemIcon><ShoppingCartIcon /></ListItemIcon>
                <ListItemText primary='Shop' />
              </ListItem>
            </List>
            )
          }
          <List>
            <ListItem button onClick={navigatePassword}>
              <ListItemIcon><KeyIcon /></ListItemIcon>
              <ListItemText primary='Enter Prompt' />
            </ListItem>
          </List>
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(AppDrawer);