import Exercise, { EXERCISE_ID, getExerciseId } from './Exercise';
import Home, { ID as HOME_ID } from './Home';
import Password, { ID as PASSWORD_ID } from './Password';
import Shop, { ID as SHOP_ID } from './Shop';

export const IDS = {
  HOME_ID, SHOP_ID, PASSWORD_ID, getExerciseId
};

export function getView(id = {}) {
  switch(id.id) {
    case HOME_ID.id: return Home;
    case SHOP_ID.id: return Shop;
    case PASSWORD_ID.id: return Password;
    case EXERCISE_ID: return Exercise;
    default: return Home;
  }
}