import { withStyles as muiWithStyles } from '@material-ui/core';

const baseStyles = theme => ({
  hbox: {
    display: 'flex',
    flexDirection: 'row',
    flexGrow: 1,
    flexShrink: 1
  },
  vbox: {
    display: 'flex',
    flexDirection: 'column',
    flexGrow: 1,
    flexShrink: 1
  },
  centered: {
    alignItems: 'center'
  },
  grow: {
    flexShrink: 1,
    flexGrow: 1
  },
  fixed: {
    flexGrow: 0,
    flexShrink: 0
  },
  scroll: {
    overflow: 'overlay'
  },
  noOverflow: {
    overflow: 'hidden'
  },
  link: {
    color: theme.mode === 'dark' ? theme.palette.secondary.light : theme.palette.secondary.dark
  },
  buttonArea: {
    background: theme.palette.background.default,
    padding: theme.spacing(1),
    justifyContent: 'flex-end'
  },
  compulsory: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.secondary.main,
    fontWeight: 800
  }
});

export type DefaultClasses = {
  hbox: string,
  vbox: string,
  centered: string,
  grow: string,
  fixed: string,
  scroll: string,
  noOverflow: string,
  link: string,
  buttonArea: string,
  compulsory: string
}

export function withStyles(styles?: any) {
  const s = styles || function() {}; // TODO MZ temp fix for typescript
  return muiWithStyles(theme => ({...baseStyles(theme), ...s(theme)}), { withTheme: true });
}

export function classNames(...classNames: string[]) {
  return classNames.join(' ');
}

