import { Button, FormControl, Modal, Select, Typography } from '@material-ui/core';
import React from 'react';

import { setMode, showChangeModePrompt } from '../persistence/state';
import { stateType } from '../persistence/state-types';
import { LEVELS, TEXTS } from '../resources/exercises';
import { classNames, DefaultClasses, withStyles } from '../utils/styles';

const width: number = 400;
const height: number = 300;

const styles = theme => ({
  modalContainer: {
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    
    position: 'absolute',
    top: '25%',
    left: '50%',
    width,
    height,
    marginLeft: width * -0.5,
    marginTop: height * -0.25
  },
  modalContent: {
    padding: theme.spacing(2, 4, 3)
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120
  },
  settingLabel: {
    width: 120,
    textAlign: 'right',
    margin: 8
  },
  title: {
    marginBottom: 20
  },
  alignRight: {
    justifyContent: 'flex-end'
  }
});

type Classes = {
  modalContainer: string,
  modalContent: string,
  formControl: string,
  settingLabel: string,
  title: string,
  alignRight: string
};

type Props = {
  classes: Classes & DefaultClasses,
  mode: stateType.Mode
};

type State = {
  pendingLevel?: string,
  pendingText?: string
};

class Settings extends React.PureComponent<Props, State> {
  constructor(props) {
    super(props);
    this.state = {
      pendingLevel: undefined,
      pendingText: undefined
    };
  }

  componentDidMount() {
    const { mode } = this.props;
    const { selectedLevel, selectedText } = mode;
    if (!selectedLevel || !selectedText) {
      showChangeModePrompt(true);
    } else {
      this.setState({
        pendingLevel: selectedLevel,
        pendingText: selectedText
      });
    }
  }
  handleLevelChange = event => {
    this.setState({
      pendingLevel: event.target.value,
      pendingText: undefined
    });
  }
  handleTextChange = event => {
    this.setState({pendingText: event.target.value});
  }
  handleSave = () => {
    const { pendingLevel, pendingText } = this.state;
    setMode({selectedLevel: pendingLevel, selectedText: pendingText});
  }
  handleClose = () => {
    showChangeModePrompt(false);
  }
  getLevelOptions = () => {
    return LEVELS.map(({NAME, ID}) => ({name: NAME, id: ID}));
  }
  getTextOptions = () => {
    const { pendingLevel } = this.state;
    const texts = TEXTS[pendingLevel] || [];
    return texts.map(({NAME, ID}) => ({name: NAME, id: ID}));
  }
  render() {
    const { classes, mode } = this.props;
    const { showPrompt, selectedLevel, selectedText } = mode;
    const { pendingLevel, pendingText } = this.state;
    return (
      <Modal
        open={!!showPrompt}
        onClose={this.handleClose}
        disableBackdropClick={true}
        disableEscapeKeyDown={true}
      >
        <div className={classNames(classes.modalContainer, classes.vbox)}>
          <div className={classNames(classes.grow, classes.modalContent)}>
            <Typography variant='h6' color='inherit' noWrap className={classes.title}>Settings</Typography>
            <div className={classes.vbox}>
              <div className={classes.hbox}>
                <Typography variant='h6' color='inherit' noWrap className={classes.settingLabel}>Exam Level</Typography>
                <FormControl className={classes.formControl}>
                  <Select native inputProps={{name: 'mode', id: 'mode-selector'}} value={pendingLevel} onChange={this.handleLevelChange}>
                    <option value={undefined}></option>
                    { this.getLevelOptions().map(({name, id}) => (<option key={id} value={id}>{name}</option>)) }
                  </Select>
                </FormControl>
              </div>
              <div className={classes.hbox}>
                <Typography variant='h6' color='inherit' noWrap className={classes.settingLabel}>Text</Typography>
                <FormControl className={classes.formControl}>
                  <Select native inputProps={{name: 'mode', id: 'mode-selector'}} value={pendingText} onChange={this.handleTextChange}>
                    <option value={undefined}></option>
                    { this.getTextOptions().map(({name, id}) => (<option key={id} value={id}>{name}</option>)) }
                  </Select>
                </FormControl>
              </div>
            </div>
          </div>
          <div className={classNames(classes.fixed, classes.hbox, classes.buttonArea)}>
            <Button
              color='primary'
              aria-label='Save'
              onClick={this.handleSave}
              disabled={!pendingLevel || !pendingText}
            >
              Save
            </Button>
            <Button
              color='secondary'
              aria-label='Cancel'
              onClick={this.handleClose}
              disabled={!selectedLevel || !selectedText}
            >
              Cancel
            </Button>
          </div>
        </div>
      </Modal>
          
    )
  }
}

export default withStyles(styles)(Settings);