import { Typography } from '@material-ui/core';
import React from 'react';

import { assistanceType, missionType } from '../../persistence/state-types';
import { withStyles } from '../../utils/styles'
import Assistance from './Assistance';
import Missions from './Missions';

const styles = theme => ({
  summary: {
    margin: theme.spacing(1)
  },
  assistance: {
    marginTop: 40
  }
});

type Classes = {
  summary: string,
  assistance: string
};

type Props = {
  classes: Classes,
  missions: missionType.Mission[],
  summary: string,
  assistance: assistanceType.Assistance
};

class Translation extends React.Component<Props> {
  render() {
    const { classes, missions, summary, assistance } = this.props;
    return (
      <div>
        <Typography className={classes.summary}>{summary}</Typography>
        <Missions missions={missions} />
        {!!assistance ? <Assistance assistance={assistance} className={classes.assistance} /> : undefined}
      </div>
    );
  }
}

export default withStyles(styles)(Translation);